body {
  margin: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;*/
  font-family: "Pathway Gothic One", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 50px;
}

img {
  max-width: 50%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-dark {
  opacity: 0.5;
  background-color: black !important;
}
.navbar-toggler {
  background-color: transparent;
  border: none;
}

.navbar-toggler-icon {
  background-image: none;
}

.text-danger {
  color: #ff4a4a !important;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #000000;
  font-size: 10px;
  color: white !important;
}

.titulo-pagina {
  border-bottom: 1px solid #f5f5f5;
  padding-bottom: 5px;
  font-size: 22px;
}

.d-flex .nav-link {
  color: white;
  text-decoration: none;
}

.d-flex .nav-link:hover {
  color: grey;
}

.nav-link:hover {
  color: #ff4a4a !important;
}

.product-title {
  text-decoration: none;
  color: black;
}

.product-title:hover {
  color: black;
  opacity: 0.5;
}

.btn.right {
  border-radius: 0;
  height: 35px;
  outline: none;
}

.btn.left {
  border-radius: 0;
  height: 35px;
}

.btn.add {
  border-radius: 0;
  height: 35px;
  width: 148px;
  margin-top: 7px;
  margin-left: -1px;
}
.btn.add:hover {
  opacity: 0.7;
}
.btn-danger {
  border-radius: 0;
  margin-left: 20px;
  border: 1px solid black;
  background-color: black;
}

.btn-danger:hover {
  background-color: white;
  color: black;

  border-bottom: 1px solid black;
}
.item-count {
  border: 1px solid black;
  max-width: 148px;
  height: 36px;
  text-align: center;
}

.item-count span {
  width: 100%;
  line-height: 35px;
}

.item-count .left {
  float: left;
}
.item-count .right {
  float: right;
}

.efecto {
  opacity: 1;
  transition: all ease 500ms;
  border-radius: 0px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 3px -2px,
    rgba(0, 0, 0, 0.14) 0px 3px 4px 0px, rgba(0, 0, 0, 0.12) 0px 1px 8px 0px;
}

.efecto:hover {
  opacity: 0.7;
  transition: all ease 150ms;
  cursor: pointer;
}

.efecto:target {
  transform: scale(0.97) translate(-100%, -100%);
}

.cart-float {
  z-index: 99999;
  background-color: black;
  position: fixed;
  bottom: 35px;
  right: 20px;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 3px -2px,
    rgba(0, 0, 0, 0.14) 0px 3px 4px 0px, rgba(0, 0, 0, 0.12) 0px 1px 8px 0px;
}

.cart-float .nav-link {
  color: white;
  text-decoration: none;
}

.cart-float .nav-link svg:hover {
  color: red;
}

.badge {
  z-index: 99999;
  background-color: black;
  color: white;
  border-radius: 100%;
  text-align: center;
  position: absolute;
  top: 22px;
  right: 55px;
}

.link-item {
  text-decoration: none;
  color: black;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.delete-padre {
  position: relative;
}

.delete {
  position: absolute;
  right: 20px;
  z-index: 999;
  border-radius: 0;
  border: 0;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 3px -2px,
    rgba(0, 0, 0, 0.14) 0px 3px 4px 0px, rgba(0, 0, 0, 0.12) 0px 1px 8px 0px;
}

.delete:hover {
  background-color: white;
  color: black;
  border: 0;
  border-bottom: 1px solid black;
}

.Toastify__toast--info {
  background-color: black;
}

.form-control {
  border-radius: 0;
  border: 1px solid black;
}

.form-control::placeholder {
  color: black;
}

textarea:hover,
input:hover,
textarea:active,
input:active,
textarea:focus,
input:focus,
button:focus,
button:active,
button:hover,
label:focus,
.btn:active,
.btn.active {
  outline: 0px !important;
  -webkit-appearance: none;
  box-shadow: none !important;
}

.btn:hover {
  border: 1px solid black !important;
}

@media (max-width: 576px) {
  .badge {
    top: 22px;
    right: 8px;
  }
}
